.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--white-color);
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border: 1px solid var(--accent-color);
}

.slider:before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;
  left: 1px;
  bottom: 1px;
  background-color: var(--accent-color);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider:before {
  background-color: var(--white-color);
}

input:checked + .slider {
  background-color: var(--accent-color);
  border: 1px solid var(--white-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--accent-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.theme__dark {
  display: none;
}

.theme__light {
  display: none;
}
@include desktop {
  .theme {
    gap: 12px;
  }

  .theme__svg {
    display: none;
  }

  .theme__dark {
    display: block;
    margin: 0;
    font-size: 18px;
    line-height: 1.3;

    color: var(--switcher-gray-moon);
  }

  .theme__light {
    display: block;
    font-size: 18px;
    line-height: 1.3;
  }

  .opacityForDark {
    opacity: 0.4;
  }
}
